.others {
    display: flex;
    width: 100%;
    max-height: 700px;
    color: black;
    flex-direction: column;
    justify-content: flex-start;
    &__text1 {
        min-height: 72px;
        width: 90%;
        color: #FFFFFF;
        font-family: "Museo Sans";
        font-size: 32px;
        letter-spacing: 0;
        line-height: 36px;
        margin-bottom: 2rem;
        animation-name: roll-in-from-bottom-smooth;
        animation-delay: 0s;
        animation-duration: 0.3s;
        animation-timing-function: ease-in-out;
        animation-fill-mode: backwards;
    }
    &__text2 {
        min-height: 48px;
        width: 85%;
        color: #FFFFFF;
        font-size: 16px;
        letter-spacing: 0;
        line-height: 24px;
        margin-bottom: 5rem;
        font-family: 'Lato', sans-serif;
        animation-name: roll-in-from-bottom-smooth;
        animation-delay: 0s;
        animation-duration: 0.3s;
        animation-timing-function: ease-in-out;
        animation-fill-mode: backwards;
    }
    &__benifits {
        min-height: 200px;
        width: 85%;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        &__box {
            display: flex;
            flex-direction: column;
            justify-content: space-around;
            align-items: flex-start;
            animation-name: roll-in-from-bottom-smooth;
            animation-delay: 0s;
            animation-duration: 0.3s;
            animation-timing-function: ease-in-out;
            animation-fill-mode: backwards;
            min-height: 160px;
            width: 27%;
            margin-bottom: 20px;
            &--image {
                max-height: 48px;
                max-width: 48px;
                margin-bottom: 5px;
                object-fit: cover;
            }
            &--name {
                color: #FFFFFF;
                font-family: Lato;
                font-size: 16px;
                letter-spacing: 0;
                line-height: 24px;
                margin-bottom: 12px;
                font-family: 'Lato', sans-serif;
            }
            &--percentage {
                width: 49px;
                color: #FFFFFF;
                font-size: 32px;
                letter-spacing: 0;
                line-height: 34px;
                font-family: 'Museo Sans';
                &--sign {
                    font-size: 16px;
                    margin-left: 5px;
                }
            }
        }
    }
    &__bottomBar {
        width: 90%;
        height: 1px;
        background-color: #FFFFFF;
        animation-name: roll-in-from-bottom-smooth;
        animation-delay: 0s;
        animation-duration: 0.3s;
        animation-timing-function: ease-in-out;
        animation-fill-mode: backwards;
    }
}

@include ss-laptop {
    .others {
        padding-left: 5%;
        margin-top: 5%;
        &__text2 {
            min-height: 0;
            height: 24px;
        }
    }
}