@include ss-laptop {
    nav {
        img {
            width: 140px;
            height: auto;
            margin-right: 12px;
        }
        button {
            width: auto;
        }
    }
}

.navbar-container {
    nav {
        width: 100%;
        height: 60px;
        background: $gradient-navbar;
        position: sticky;
        top: 0;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
        padding: 0 2em 0 2em;
        border-bottom: 0px solid black;
        overflow: hidden;
        img {
            height: 35px;
            object-fit: contain;
            display: block;
        }
        button {
            @include mobile-tablet {
                width: 96px;
            }
            width: 200px;
            height: 40px;
            background-color: $button-backgroud-color;
            border: 1px solid white;
            color: white;
            border-radius: 5px;
            text-transform: uppercase;
            &:hover {
                cursor: pointer;
            }
            &:active {
                transform: translateY(-0.1rem);
                box-shadow: 0 0.5rem 1rem black;
            }
            &::after {
                content: "";
                display: inline-block;
                height: 100%;
                width: 100%;
                border-radius: 10rem;
                position: absolute;
                z-index: -1;
                top: 0;
                left: 0;
                transition: all 0.4s;
            }
        }
    }
}